import { Component, OnInit } from '@angular/core';
import { AssetAssetService, AssetPartIdService } from 'src/app/sdk/asset/services';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute } from '@angular/router';
import { Part_id } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private assetAssetService: AssetAssetService,
        private assetPartIdService: AssetPartIdService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private activeRouter: ActivatedRoute
    ) { }

    partId: Part_id = {}
    id_part_id = ''
    filter: any = {}
    ngOnInit() {

        this.activeRouter.paramMap.subscribe(
            data => {
                console.log(data.get('id_part_id'))
                this.filter.id_part_id = null;
                this.id_part_id = null;
                if (data.get('id_part_id')) {
                    this.id_part_id = data.get('id_part_id');
                    this.filter.id_part_id = this.id_part_id;
                    this.searchData()
                    this.getPartId()
                }
            }
        )
    }

    getPartId() {
        this.assetPartIdService.getAssetPartIdsId(this.id_part_id).subscribe(
            data => {
                this.partId = data.data;
            }
        )
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {

        this.spinner.show()
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.assetAssetService
            .getAssetAssets(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search, id_part_id: this.id_part_id
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
                this.spinner.hide()
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }


}
