import { ListComponent } from './list/list.component';
import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { AssetRoutingModule } from './asset-routing.module'
import { AssetComponent } from './asset.component'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar'
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module'
import { NzLayoutModule, NgZorroAntdModule } from 'ng-zorro-antd'
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { LayoutModule } from 'src/app/components/layout/layout.module'
import { ApiModule } from 'src/app/sdk/asset/api.module';
import { ApiModule as ApiModuleHr } from 'src/app/sdk/hr/api.module';
import { ApiModule as ApiModuleUser } from 'src/app/sdk/user/api.module';
import { FormsModule } from '@angular/forms'
import { SearchItemModule } from 'src/app/components/search-item/search-item.module';
import { PipeModule } from 'src/app/pipe/pipe.module';

@NgModule({
    declarations: [AssetComponent, ListComponent],
    imports: [
        CommonModule,
        AssetRoutingModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-asset.vm.devetek.com/api' }),
        ApiModuleHr.forRoot({ rootUrl: 'https://erp-etos.devetek.site:8443/hr/api' }),
        ApiModuleUser.forRoot({ rootUrl: 'https://erp-etos.devetek.site:8443/hr/api' }),
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        NgZorroAntdModule,
        FormsModule,
        SearchItemModule,
        PipeModule
    ],
    providers: [FormBuilderTypeSafe],
})
export class AssetModule { }
