import { AssetComponent } from './asset.component'
import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { ListComponent } from './list/list.component'

const routes: Routes = [
    {
        path: '',
        component: AssetComponent,
        children: [
            {
                path: 'model/:id_model',
                loadChildren: () =>
                    import('./model-detail/model-detail.module').then(mo => mo.ModelDetailModule),
            },
            {
                path: 'list/:id_part_id',
                component: ListComponent
            },
            {
                path: 'detail/:id_asset',
                loadChildren: () =>
                    import('./asset-detail/asset-detail.module').then(mo => mo.AssetDetailModule),
            },



            {
                path: 'transaction',
                loadChildren: () =>
                    import('./transaction/transaction.module').then(mo => mo.TransactionModule),
            },
            {
                path: 'archived',
                loadChildren: () =>
                    import('./archived/archived.module').then(mo => mo.ArchivedModule),
            }
        ],

    },
    {
        path: 'master',
        loadChildren: () =>
            import('./master/master.module').then(mo => mo.MasterModule),
    },
    {
        path: 'management',
        loadChildren: () =>
            import('./management/management.module').then(mo => mo.ManagementModule),
    },
    {
        path: 'maintenance',
        loadChildren: () =>
            import('./maintenance/maintenance.module').then(mo => mo.MaintenanceModule),
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class AssetRoutingModule { }
